import * as types from './mutations-types'
import GIF from 'gif.js'

const gif = new GIF({
  workers: 1,
  quality: 10
})

export default {
  [types.UPDATE_FRAME_SIZE] (state, { size }) {
    state.frame.width = size.width || state.frame.width
    state.frame.height = size.height || state.frame.height
  },
  [types.ADD_FRAME] (state, { image }) {
    console.log('NEW IMAGE', state.frame.gif)
    gif.addFrame(image)
// // or copy the pixels from a canvas context
// state.gif.addFrame(ctx, {copy: true});
    gif.on('finished', (blob) => {
      console.log('YAY')
      // window.open(URL.createObjectURL(blob))

      let img = new Image()
      img.src = URL.createObjectURL(blob)
      img.onload = () => {
        // this.$store.dispatch('addFrame', img)
        document.body.appendChild(img)
      }
    })

    gif.render()
// state.gif.on('finished', function(blob) {
//   window.open(URL.createObjectURL(blob));
// });
  }
  // ,
  // // [types.SET_RENDERER] (state, { renderer }) {
  // //   console.log('RENDERER:', renderer)
  // //   state.frame.renderer = renderer
  // // }

}



